import { Component, Output, EventEmitter } from '@angular/core';
import { HttpService } from '../Data/http.service'
import { Note } from '../Data/Note';
import { JwtHelperService } from '@auth0/angular-jwt';
import { HttpHeaders, HttpClient } from '@angular/common/http';

@Component({
    selector: 'notelist',
    templateUrl: 'app.notelist.html',
    styleUrls: ['app.notelist.css'],
    providers: [HttpService],
})

export class AppNotelist {

    @Output() onChangedUserName = new EventEmitter<string>();
    notelist: Array<Note> = [];
    constructor(private httpService: HttpService, private jwtHelper: JwtHelperService, private http: HttpClient) { }

    createNote(): void {
        if (!this.canActivate()) return 

        let note: Note = new Note("", this.formateDate(new Date), null)

        this.httpService.save(note).subscribe((id: any) => {
            note.Id = id
        })

        this.notelist.unshift(note)
        setTimeout(() => {
            note.Selected = true
        }, 100);
    }

    update(note: Note): void {
        if (!this.canActivate()) return 
        this.httpService.update(note).subscribe()
        note.Selected = false;
        note.TempText = null;
    }

    del(note: Note, index: number): void {
        if (!this.canActivate()) return 
        this.httpService.delete(note).subscribe()
        note.Selected = false;
        this.notelist.splice(index, 1)
    }


    selectNote(note: Note): void {
        if (!this.canActivate()) return 
        note.Selected = true;
        note.TempText = note.Text
    }

    unselectNote(note: Note, index: number): void {
        if (!this.canActivate()) return 
        note.Selected = false;
        note.Text = note.TempText
        note.TempText = null;
    }

    loadNotes(): void {
        if (!this.canActivate()) return 
        this.httpService.loadNotes().subscribe((data: any) => {
            this.notelist = data.notes
            this.onChangedUserName.emit(data.name)
        })
    }

    ngOnInit(): void {
        this.loadNotes()
    }

    formateDate(inDate: Date): string {
        const options = {
            month: 'long',
            day: 'numeric',
            hour: 'numeric',
            minute: 'numeric',
        };
        return inDate.toLocaleString('ru', options);
    }

    async canActivate() {
        const token = localStorage.getItem("jwt");

        if (token && !this.jwtHelper.isTokenExpired(token)) {
            return true;
        }

        const isRefreshSuccess = await this.tryRefreshingTokens(token);
        if (!isRefreshSuccess) {
            this.onChangedUserName.emit("")
        }
    }


    private async tryRefreshingTokens(token: string): Promise<boolean> {
        // Try refreshing tokens using refresh token
        const refreshToken: string = localStorage.getItem("refreshToken");
        const credentials = JSON.stringify({ accessToken: token, refreshToken: refreshToken });
        let isRefreshSuccess: boolean;
        try {
            const response = await this.http.post("http://localhost/api/token/refresh", credentials, {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                }),
                observe: 'response'
            }).toPromise();
            // If token refresh is successful, set new tokens in local storage.
            const newToken = (<any>response).body.accessToken;
            const newRefreshToken = (<any>response).body.refreshToken;
            localStorage.setItem("jwt", newToken);
            localStorage.setItem("refreshToken", newRefreshToken);
            isRefreshSuccess = true;
        }
        catch (ex) {
            isRefreshSuccess = false;
        }
        return isRefreshSuccess;
    }

}


